.wrapperCustomersAll{
    margin: 0 auto;
    width: 968px;
    min-height: 600px;
    background: #FFFFFF;
    box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.5);
    border-radius: 30px;
    padding: 30px 38px;
}

.wrapAllSearch{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 130px;
}

.titleAllCustomers{
font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 22px;
line-height: 33px;
letter-spacing: -0.01em;
color: #000000;
}

.titleActiveMembers{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: -0.01em;
    color: #16C098;
    margin-top: 7px;
}